$mediaLargeMinWidth:1024px;
$mediaMediumMinWidth:768px;
$mediaSmallMinWidth:640px;

.search {
	.site-main{
		.entry-content{
			.main-container{
				h3{
					font-weight: 600;
				}
			}
		}
		.dash{
			border: 1px solid #00aab9;
			width: 70%;
			height: 0px;
			margin: auto;
			margin-bottom: 15px;
			&:last-child{
				display: none;
			}
	
	}

	}

}
.block-search{
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	overflow: auto;
	text-align: center;
	background: rgba(0,0,0,0.9);
	margin: 0;
	z-index: 999;
		.centered{
			width: 50%;
			padding: 10px 15px;
			color: #fff;
			border: none;
			background: transparent;
			display: inline-block;
			vertical-align: middle;
				#search-box{
					input[type=text]{
						width: 90%;
						color: #333;
						outline: none;
						font-size: 1.4em;
						padding: 20px 45px 20px 20px;
					}
					#close-btn {
						position: fixed;
						top: 1em;
						right: 1em;
						&:hover{
							cursor: pointer;
						}
					}
					.fa-search{
						position: absolute; 
						bottom: 50px;
						left: 230px; 
						color: black;
					}
					.search-btn-icon{
						position: relative;
					}
				}
		}
		&:before{
			content: "";
			height: 100%;
			margin-right: 0;
			display: inline-block;
			vertical-align: middle;
		}
}
.block-search-page{
	#search-box{
		input[type=text]{
			width: 70%;
			color: #333;
			outline: none;
			font-size: 1.4em;
			padding: 20px 45px 20px 20px;
		}
		#close-btn {
			position: fixed;
			top: 1em;
			right: 1em;
			&:hover{
				cursor: pointer;
			}
		}
		.fa-search{
			position: absolute; 
			bottom: 55px;
			left: 640px; 
			color: black;
		}
		.search-btn-icon{
			position: relative;
		}
	}
}
	
	
.four-column-image-group{
	padding: 0 6rem 3.25rem;
	background-color: #f7f7f7;
	.single-main-col{
		margin: 0;
	}
	.four-col-inner{
		background-color: #fff;
		padding: 1rem;
		margin-bottom: 1rem;
		.desc{
			cursor: pointer;
			font-size: 1rem;
			text-align: center;
			display: inline-block;
			position: relative;
			text-decoration: none;
			&:after{
				content: '';
				display: block;
				border-bottom: solid 3px #00aab9;
				transform: scaleX(0);
				transition: transform 0.75s ease-in-out;
				transform-origin: 100% 50% ;
			}
			&:hover{
				&:after{
					transform: scaleX(1);
					transform-origin: 0 50% ;
				}
			}
		}
		.date{
			text-align: center;
			font-size: 0.8rem;
			padding-bottom: 0.5rem;
		}
	}
}

.single{
	.container{
		p{
			font-size: 12pt;
			line-height: 2rem;
			margin-bottom: 1rem;
		}
	}
}
.gform_confirmation_wrapper{
	.gform_confirmation_message{
		color: #3CB371;
	}
}
.page-template-media-release, .page-template-chief-executive, .page-template-policy-submission{
	.years-list{
		margin-bottom: 2rem;
		.year{
		    padding: .2rem .5rem;
		    margin-right: 0.2rem;
		    &.active{
		    	background-color: #00aab9;
			    padding: .2rem .5rem;
			    margin-right: 0.2rem;
			    a{
			    	color: #fff;			    	
			    }
		    }
			&:hover{
			    background-color: #eee;
			    padding: .2rem .5rem;
			    margin-right: 0.2rem;
			    a{
			    	color: #00aab9;	
			    	text-decoration: none;		    	
			    }
			}
		    a{
		   	 color: #1A2C69;		    	
		    }
		}
	}
	#archives{
		width: 100% ;
		.archive_year{
			width: 4.3rem;
			background-color: #00aab9;
			color:#fff;
			padding: 0.6rem 0.9rem;
		}
		.archives_list{
			.list-media{
				.linked{
					padding-right: 10px;
					a{
						color: #337ab7;
						text-decoration: none;
					}
				}
				&:after{
					content: "";
	                display: block;
	                height: 1px;
	                background: #ddd;
	                width: 53rem;
	                left: 100%;
	                margin: 1rem 0;
	                top: calc(50% - 2px);
				}
			}
			.list-heading{
				&:after{
					content: "";
	                display: block;
	                height: 1px;
	                background: #000;
	                width: 53rem;
	                left: 100%;
	                margin: 1rem 0;
	                top: calc(50% - 2px);
				}
			}
		}
	}
}
.page-template-standard-content{
	.no-image{
		.inner-wrap{
			margin-top: 0;
		}
			
	}
	.is_image{
		.inner-wrap{
			margin-top: 30px;
		}
		
	}
	.content-block{
		.filter-menu{
			margin-bottom: 2rem;
			ul{
				display: flex;
				flex-wrap: wrap;
				li{
					background-color: #00aab9;
				    color: #fff;
				    padding: .2rem .5rem;
				    margin-right: .2rem;
				    margin-bottom: .2rem;
				    a{
				    	color: #fff;
				    }
				}
			}
		}
		.wp-block-table{
			font-size: 0.8rem;
		}
		ol {
		  list-style: none;
		  counter-reset: ol-counter;
		  	>li{
  			 counter-increment: ol-counter;
  				&:before{
				  content: counter(ol-counter) ". ";
				  color: #1A2C69;
				  font-weight: bold;  					
  				}
                ul{
                    margin-left: 3rem;
                }
		  	
		  }
		}
		a{
			font-weight: 700;
		}
		.gform_wrapper{
			select, textarea, input{
				border: 0.5px solid #e2e8f0;
				height: 2.5rem;
				border-radius: 10px;
				outline: none;
				&:focus{
					box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.5);
				}
			}
			textarea{
				width: 50%;
				height: 10rem;
			}
			.validation_error{
				display: none;
			}
			.gfield_error{
				background-color: unset;
				border-top: unset;
				border-bottom: unset;
				padding: 0;
				.gfield_label{
					color: black;
					margin: 0;
				}
				.ginput_container{
					margin: 0;
				}
				.validation_message{
					padding: 0;
				}
			}
			.gform_footer{
				input[type=submit]{
				background-color: #00aab9;
				padding: 0.8rem 2rem 2rem 2rem;
				color: #fff;
				font-weight: 800;
				line-height: 1.25rem;
				font-size: 1rem;

			    background-size: 100% 200%;
			    background-image: linear-gradient(to bottom, #00aab9 50%, #1A2C69 50%);
			    -webkit-transition: background-position .3s;
			    -moz-transition: background-position .3s;
			    transition: background-position .3s;
				    &:hover{
				    	background-position: 0 90%;
				    }
				}
			}
			.gfield_required{
				color: black;
			}
		}
	}
}
