						.fa-search{
							left: 85px;						
						}
					}
				}
			}
		}
		.block-search-page{
			#search-box{
				input[type=text]{
					width: 100%;
				}
				.fa-search{
					left: 215px;
				}
			}
		}
		&.page-template-standard-content{
			.content-block{
